<template>
	<div class="give-badge-main">
		<b-row>
			<b-col lg="8">
				<div
					:class="[
						'box',
						error.selectedItems ? 'error' : ''
					]"
				>
					<h2 class="box-header">
						Student list
					</h2>
					<div class="table">
						<div class="header">
							<b-button-group class="search">
								<b-form-input
									v-model="search"
									placeholder="Search here"
									size="sm"
								/>
								<b-button
									variant="primary"
									size="sm"
								>
									Search
								</b-button>
							</b-button-group>
							<b-button-group>
								<b-button
									variant="outline-primary"
									size="sm"
								>
									Month
								</b-button>
								<b-button
									variant="outline-primary"
									size="sm"
								>
									Year
								</b-button>
								<b-button
									variant="outline-primary"
									size="sm"
								>
									Semesterr
								</b-button>
							</b-button-group>
						</div>
						<b-table
							ref="selectableTable"
							:items="items"
							:fields="fields"
							selectable
							select-mode="multi"
							selected-variant="primary"
							responsive="sm"
							@row-selected="onRowSelected"
						>
							<template
								slot="[selected]"
								slot-scope="{ rowSelected }"
							>
								<template v-if="rowSelected">
									<span aria-hidden="true">
										<icon-check />
									</span>
									<span class="sr-only">Selected</span>
								</template>
								<template v-else>
									<span aria-hidden="true" />
									<span class="sr-only">Not selected</span>
								</template>
							</template>
						</b-table>
						<div class="footer">
							<div class="text-center">
								<b-button
									class="mr-2"
									variant="primary"
									size="sm"
									@click="selectAllRows"
								>
									Select all
								</b-button>
								<b-button
									variant="outline-primary"
									size="sm"
									@click="clearSelected"
								>
									Clear selected
								</b-button>
							</div>
							<div class="mt-3">
								<b-pagination
									v-model="currentPage"
									:total-rows="rows"
									:per-page="perPage"
									aria-controls="my-table"
									align="center"
									size="sm"
								/>
							</div>
						</div>
					</div>
				</div>
			</b-col>
			<b-col lg="4">
				<div class="box scrollable">
					<h2 class="box-header">
						Selected student
					</h2>
					<div class="box-content scrollable">
						<ul class="selected">
							<li
								v-for="(item, index) in selectedItems"
								:key="`${item}${index}`"
								class="item"
							>
								{{ item.studentId }}
								<button
									class="delete"
									@click="deleteSelectedRow(item.studentId)"
								>
									<icon-cross-circle />
								</button>
							</li>
						</ul>
					</div>
				</div>
			</b-col>
		</b-row>
		<base-page-step
			:step="step"
			@next="submit"
		/>
	</div>
</template>
<style lang="scss">
@import "@/styles/pages/give-badge-main.scss";
</style>
<script>
import IconCheck from "@/components/icons/IconCheck.vue";
import IconCrossCircle from "@/components/icons/IconCrossCircle.vue";
import { mapState } from "vuex";

export default {
	components: {
		IconCheck,
		IconCrossCircle
	},
	data() {
		return {
			currentPage: 1,
			perPage: 3,
			search: "",
			fields: ["selected", "studentId", "fullName"],
			items: [
				{ studentId: 59130500210, fullName: "Dickerson Macdonald" },
				{ studentId: 59130500445, fullName: "Larsen Shaw" },
				{ studentId: 59130500222, fullName: "Geneva Wilson" },
				{ studentId: 59130522033, fullName: "Jami Carney" }
			],
			selectMode: "multi",
			selectedItems: [],
			error: {
				selectedItems: false
			}
		};
	},
	computed: {
		...mapState("base", [
			"students"
		]),
		...mapState("giveBadge", [
			"selectedStudents",
			"steps"
		]),
		hasSelectedItem() {
			if (this.selectedItems.length > 0) {
				return true;
			}

			return false;
		},
		step() {
			return this.$route.meta.step;
		},
		rows() {
			return this.items.length;
		}
	},
	watch: {
		selectedItems() {
			this.error.selectedItems = false;
		}
	},
	beforeRouteEnter(to, from, next) {
		next((vm) => {
			if (vm.students.length === 0) {
				vm.$store.dispatch("base/loadStudentData");
			}
		});
	},
	created() {
		// TODO: Get items from server
		this.selectedItems = this.selectedStudents;
		if (this.steps.includes("selection")) {
			this.setUpSelectedItems();
		}
	},
	mounted() {
		this.selectedItems.forEach((item) => {
			const index = this.items.findIndex((i) => i.studentId === item.studentId);
			this.$refs.selectableTable.selectRow(index);
		});
	},
	methods: {
		setUpSelectedItems() {
			this.items.forEach((item, index) => {
				this.selectedStudents.forEach((student) => {
					if (item.studentId === student.studentId) {
						this.items[index] = student;
					}
				});
			});
		},
		onRowSelected(items) {
			this.selectedItems = items;
		},
		selectAllRows() {
			this.$refs.selectableTable.selectAllRows();
		},
		clearSelected() {
			this.$refs.selectableTable.clearSelected();
		},
		deleteSelectedRow(id) {
			const index = this.items.findIndex((item) => item.studentId === id);
			this.$refs.selectableTable.unselectRow(index);
		},
		async submit() {
			if (!this.hasSelectedItem) {
				this.error.selectedItems = true;
				this.$bvToast.toast("Please select at least one student to give", {
					title: "No student error",
					variant: "danger",
					autoHideDelay: 1500
				});
				return;
			}

			await this.$store.dispatch("giveBadge/updateSelectedStudents", this.selectedItems);
			await this.$store.dispatch("giveBadge/addStep", this.step.step);
			this.$router.push({ name: "give-badge-selection" });
		}
	}
};
</script>